import './App.css';

import React, { useState, useEffect } from "react";
import DatePicker, { registerLocale } from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";
import ko from 'date-fns/locale/ko';

import Container from 'react-bootstrap/Container';
import Navbar from 'react-bootstrap/Navbar';
import ListGroup from 'react-bootstrap/ListGroup';

import PriceTable from './PriceTable';

registerLocale('ko', ko);

function App() {

  const [startDate, setStartDate] = useState(new Date());
  const [endDate, setEndDate] = useState(new Date());

  const [minTime, setMinTime] = useState(new Date());
  const [maxTime, setMaxTime] = useState(new Date());

  const [days, setDays] = useState(0);
  const [hours, setHours] = useState(0);
  const [selected, setSelected] = useState(false);

  function dateCalculator(startDate, endDate) {
    const totalHours = (endDate - startDate) / (1000 * 3600)
    const calculatedDays = Math.floor(totalHours / 24)
    const calculatedHours = (totalHours % 24).toFixed(1);

    setDays(calculatedDays)
    setHours(calculatedHours)
  }

  useEffect(() => {

    startDate.setDate(startDate.getDate()+2);
    startDate.setHours(10);
    startDate.setMinutes(0);

    endDate.setDate(startDate.getDate()+2);
    endDate.setHours(10);
    endDate.setMinutes(0);

    minTime.setHours(8);
    minTime.setMinutes(0);

    maxTime.setHours(18);
    maxTime.setMinutes(30);
  }, [])

  useEffect(() => {
    dateCalculator(startDate, endDate)
  }, [startDate, endDate])

  useEffect(() => {
    if (startDate > endDate) {
      alert("      .")
      setEndDate(startDate)
    }
  }, [endDate])

  useEffect(() => {
    if (startDate > endDate) {
      setEndDate(startDate)
    }
  })

  return (
    <div className="App">
      <meta charSet="utf-8" />
      <title>  </title>
      <Navbar style={{ backgroundColor:'#343A3F' }}>
        <Container>
          <Navbar.Brand>
            <span style={{ color:'#FFFFFF' }}>  </span>
            <span style={{ fontSize: "15px", color:'#FFFFFF' }}></span>
          </Navbar.Brand>
        </Container>
      </Navbar>
      <div className='body-section'>
        <div className='body-row' >
          <ListGroup>
            <ListGroup.Item className='list-group-item'>
              <h6></h6>
              <DatePicker
                className='date-picker'
                selected={startDate}
                onChange={(date) => {
                  setStartDate(date);
                }}
                minDate={startDate}
                popperProps={{
                  positionFixed: true
                }}
                popperModifiers={{
                  preventOverflow: {
                    enabled: true,
                }}}
                dateFormat=" yyyy-MM-dd"
                locale='ko' />

            </ListGroup.Item>
            <ListGroup.Item className='list-group-item'>
              <h6></h6>
              <DatePicker
                className='date-picker'
                selected={startDate}
                onChange={(date) => {
                  setStartDate(date);
                  setSelected(true);
                }}
                showTimeSelect
                showTimeSelectOnly
                dateFormat=" HH:mm"
                // minTime={minTime}
                // maxTime={maxTime}
                timeIntervals={30}
                popperProps={{
                  positionFixed: true
                }}
                popperModifiers={{
                  preventOverflow: {
                    enabled: true,
                }}}
                timeCaption=""
                locale='ko' />
            </ListGroup.Item>
            <ListGroup.Item className='list-group-item'>
              <h6></h6>
              <DatePicker
                className='date-picker'
                selected={endDate}
                onSelect={(date) => {setEndDate(date)}}
                minDate={startDate}
                popperProps={{
                  positionFixed: true
                }}
                popperModifiers={{
                  preventOverflow: {
                    enabled: true,
                }}}
                dateFormat=" yyyy-MM-dd"
                locale='ko' />
            </ListGroup.Item>
            <ListGroup.Item className='list-group-item'>
              <h6></h6>
              <DatePicker
                className='date-picker'
                selected={endDate}
                onChange={(date) => {
                  setEndDate(date)
                  setSelected(true)
                }}
                showTimeSelect
                showTimeSelectOnly
                timeIntervals={30}
                // excludeTimes={excludedTime}
                popperProps={{
                  positionFixed: true
                }}
                popperModifiers={{
                  preventOverflow: {
                    enabled: true,
                }}}
                timeCaption=""
                dateFormat=" HH:mm"
                locale='ko' />
            </ListGroup.Item>
            <ListGroup.Item className='list-group-item' style={{ backgroundColor: '#F5F5F5' }}>
              <h6></h6>
              <div style={{ fontWeight:'800' }}>
                {hours > 0 ? days+1:days}
              </div>
            </ListGroup.Item>
          </ListGroup>
          <div className='space'/>
          <div style={{ margin:'auto', width:'670px' }}>
            <div style={{ textAlign:'left', color: 'red', fontWeight:600 }}>
              <div>*    .</div>
			  <div>*  7       . (tillpark3@naver.com)</div>
            </div>
            <PriceTable
              category=""
              reservePrice={10} // 
              onSitePay={45} // 
              onSitePayInsurance={63} //  (+)
              defaultPrice={55} //
              defaultPriceInsurance={73} // (+)
              days={days}
              hours={hours} />
            <PriceTable
              category=""
              reservePrice={10}
              onSitePay={50}
              onSitePayInsurance={76}
              defaultPrice={60}
              defaultPriceInsurance={86}
              days={days}
              hours={hours} />
            <PriceTable
              category=""
              reservePrice={10}
              reservePriceInsurance={12}
              onSitePay={59}
              onSitePayInsurance={85}
              defaultPrice={69}
              defaultPriceInsurance={97}
              days={days}
              hours={hours} />
            <PriceTable
              category="SUV"
              reservePrice={11}
              onSitePay={66}
              onSitePayInsurance={78}
              defaultPrice={77}
              defaultPriceInsurance={89}
              days={days}
              hours={hours} />
            <PriceTable
              category=""
              reservePrice={20}
              onSitePay={145}
              onSitePayInsurance={165}
              defaultPrice={165}
              defaultPriceInsurance={185}
              days={days}
              hours={hours} />
          </div>
        </div>
      </div>
      <footer style={{ backgroundColor:'#343A3F', color:'#ffffff', paddingTop:'16px', paddingBottom:'16px', fontWeight:600 }}>Copyright   2020</footer>
    </div>
  );
}

export default App;
