import Table from 'react-bootstrap/Table';

function PriceTable(props) {
  
  return (
    <div>
      <hr />
      <br />
      <h3 style={{ textAlign:'left', fontWeight:500 }}>
        {props.category}
      </h3>
      <br />
      <h6></h6>
      <Table bordered style={{ fontSize: '13px' }}>
        <thead>
          <tr style={{ backgroundColor: '#F5F5F5' }}>
            <th colSpan={2}> <a style={{ color: 'red' }}>(1)</a></th>
            <th colSpan={2}> + (ZDC) <a style={{ color: 'red' }}>(2)</a></th>
          </tr>
        </thead>
        <tbody>
          <tr>
            <td colSpan={2}>
              ${props.hours > 0 ?
              ((props.reservePrice+props.onSitePay)*(props.days+1)).toFixed(1):
              ((props.reservePrice+props.onSitePay)*props.days).toFixed(1)}
            </td>
            {props.reservePriceInsurance ?
              <td colSpan={2}>
                ${props.hours > 0 ?
                  ((props.reservePriceInsurance + props.onSitePayInsurance) * (props.days + 1)).toFixed(1) :
                  ((props.reservePriceInsurance + props.onSitePayInsurance) * (props.days)).toFixed(1)}
              </td>
              : <td colSpan={2}>
                ${props.hours > 0 ?
                  ((props.reservePrice + props.onSitePayInsurance) * (props.days + 1)).toFixed(1) :
                  ((props.reservePrice + props.onSitePayInsurance) * (props.days)).toFixed(1)}
              </td>}
          </tr>
        </tbody>
        <thead>
          <tr style={{ backgroundColor: '#F5F5F5' }}>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
          </tr>
        </thead>
        <tbody>
          {props.reservePriceInsurance ?
            <tr>
              <td id='price1'>${props.hours > 0 ? (props.reservePrice * (props.days + 1)).toFixed(1) : (props.reservePrice * props.days).toFixed(1)}</td>
              <td id='price2'>${props.hours > 0 ? (props.onSitePay * (props.days + 1)).toFixed(1) : (props.onSitePay * (props.days)).toFixed(1)}</td>
              <td id='insurance-price1'>${props.hours > 0 ? (props.reservePriceInsurance * (props.days + 1)).toFixed(1) : (props.reservePriceInsurance * props.days).toFixed(1)}</td>
              <td id='insurance-price2'>${props.hours > 0 ? (props.onSitePayInsurance * (props.days + 1)).toFixed(1) : (props.onSitePayInsurance * (props.days)).toFixed(1)}</td>
            </tr>
            : <tr>
              <td id='price1'>${props.hours > 0 ? (props.reservePrice * (props.days + 1)).toFixed(1) : (props.reservePrice * props.days).toFixed(1)}</td>
              <td id='price2'>${props.hours > 0 ? (props.onSitePay * (props.days + 1)).toFixed(1) : (props.onSitePay * (props.days)).toFixed(1)}</td>
              <td id='insurance-price1'>${props.hours > 0 ? (props.reservePrice * (props.days + 1)).toFixed(1) : (props.reservePrice * props.days).toFixed(1)}</td>
              <td id='insurance-price2'>${props.hours > 0 ? (props.onSitePayInsurance * (props.days + 1)).toFixed(1) : (props.onSitePayInsurance * (props.days)).toFixed(1)}</td>
            </tr>}
        </tbody>
      </Table>
      <br />
    </div>
  )
}

export default PriceTable;